
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
XInput,
XButton,
Toast,
Confirm
},
data () {
return {
name:'',
tips: '',
answer: '两次号码输入不一致，请重新输入。',
showcancel: false,
cancel: '重听',
congfirm:'确定',
show: false,

sessionId:'',
params:{
'password':'',
'sessionId':''
},
showPositionValue: false,
msg: '请输入姓名',
middle:'middle',
pwd01:'',
pwd02:''
}
},
created () {
this.params.sessionId = getCookie('sessionId')
console.log(this.params.sessionId)
},
methods: {
change (val, label) {
console.log('change', val, label)
},
onComfirmTap (e){
console.log(this.params)
const that = this
console.log(this.pwd01)
console.log(this.pwd02)
if(this.pwd01 == this.pwd02){
that.params.password = that.pwd01
this.updatePassword()
}else {
that.show = true
}

},
updatePassword (e){
const that = this;
Drive.prototype.updatePassword(that.params).then(res => {
let list = res.data.data || [];
that.$router.push('../tealogin')
console.log(list)
});
},
onCancel () {
console.log('on cancel')
},
onConfirm (msg) {
console.log('on confirm')
if (msg) {
alert(msg)
}
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
}
}



